import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import mu from '../../lib/musher-util';

const fieldSorter = (fields) => (a, b) => fields.map(o => {
    let dir = 1;
    if (o[0] === '-') { dir = -1; o=o.substring(1); }
    return a[o] > b[o] ? dir : a[o] < b[o] ? -(dir) : 0;
}).reduce((p, n) => p ? p : n, 0);

@observer
class RaceLiveCheckpointResult extends Component {
    chooseSubmenu = (e) => {
		const submenu = e.target.closest('button').dataset.submenu;
		this.setState({ submenu });
	}

    toggleInfo = (e) => {
        const { showInfo } = this.state;
		this.setState({ showInfo: !showInfo });
	}

    render() {
        const { id, onClickNav = () => {} } = this.props;
        const { submenu = 'detailsIn', showInfo } = this.state;

        const { appState, raceStore, userStore } = this.props.stores;
        const { user: currentUser = {}, teams = [], isAdmin } = userStore;
        const { language = 'en' } = currentUser;

        const { selectedClassIds = [] } = appState;
        const selectedClassId = selectedClassIds[0];
        const currentClass = raceStore.getClassById(selectedClassId);

        let selectedCheckpoint = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.id === id) : null;
        selectedCheckpoint = selectedCheckpoint ? selectedCheckpoint : {};
        const { sortedContestants = [] } = raceStore;

        const allResultsCp = currentClass.results ? currentClass.results.slice().sort(fieldSorter(['timestamp', '-direction'])).filter(e => e.checkpoint === id) : null;

        const lastResultsIn = allResultsCp ? allResultsCp.filter(e => e.direction === 'in') : null;
        const lastResultsOut = allResultsCp ? allResultsCp.filter(e => e.direction === 'out') : null;
        const lastResultsLegtimes = allResultsCp ? allResultsCp.slice().sort(fieldSorter(['legTime', 'timestamp'])).filter(e => e.direction === 'in') : null;

        const lastResultsScratched = allResultsCp ? allResultsCp.filter(e => e.direction === 'scratched') : null;

        const prevCp = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.sortOrder === selectedCheckpoint.sortOrder - 1) : null;
        const nextCp = currentClass.checkpoints ? currentClass.checkpoints.find(e => e.sortOrder === selectedCheckpoint.sortOrder + 1) : null;
        const prevResultsOut = currentClass.results && prevCp ? currentClass.results.slice().filter(e => e.checkpoint === prevCp.id && e.direction === 'out') : null;
        const nextResultsIn = currentClass.results && nextCp ? currentClass.results.slice().filter(e => e.checkpoint === nextCp.id && e.direction === 'in') : null;

        const image = selectedCheckpoint.image;
        const topImage = selectedCheckpoint.topImage;
        const shortname = mu.displayNameShort(selectedCheckpoint);
        const dotColor = ''; // mu.getRankingColor(selectedCheckpoint.sortOrder);

        const defaultImage = {
            s3XXLargeLink: './assets/checkpoint.png',
        };
        const headerImage = topImage ? topImage : defaultImage;

        return (<>
            <div class='w-100 position-absolute'
                style={`z-index: -1; height: 18vh; top: 0px; left: 0px; background-size: cover; background-repeat: no-repeat; background-position: 50% 50%; background-color: #a0a0a0; ${headerImage ? `background-image: url("${headerImage.s3XXLargeLink}");` : ''}`}
            />

            {nextCp && <button class='btn btn-link text-primary float-right mr-2 py-0' style='font-size: 2.0em; line-height: 1.0em;' onClick={() => onClickNav(nextCp.id)}>
                <i class='fas fa-angle-right' />
            </button>}
            {prevCp && <button class='btn btn-link text-primary float-right mr-2 py-0' style='font-size: 2.0em; line-height: 1.0em;' onClick={() => onClickNav(prevCp.id)}>
                <i class='fas fa-angle-left' />
            </button>}

            <div class='d-flex position-relative' style='margin-top: 16vh;'>
                <div
                    class={`position-relative text-center border rounded-circle imageRounded`}
                    style={`margin-top: -50px; ${image ? `background-image: url("${image}"); background-size: cover;` : 'background-color: #c0c0c0;'}`}
                >
                    {!image && <div class='text-muted' style='font-size: 35px; padding-top: 10px;'>{shortname}</div>}
                    <div
                        class={`rounded-pill-both px-2 text-center position-absolute ${dotColor ? '' : 'bg-secondary'} font-weight-bold`}
                        style={`bottom: -8px; right: 0; ${dotColor ? `background-color: ${dotColor};` : ''}`}
                    >
                        {selectedCheckpoint.sortOrder && selectedCheckpoint.sortOrder !== 'n/a' && <>{selectedCheckpoint.sortOrder}</>}
                    </div>
                </div>
                <div class='flex-grow-1 pl-3 text-overflow mb-3' style='line-height: 1.2em;'>
                    <h3 class='mb-2'>
                        {selectedCheckpoint.name}
                    </h3>
                    <div>
                        {selectedCheckpoint.cellphone && <><span class='text-muted'><i class='fas fa-phone' /></span> <a href={`tel:${selectedCheckpoint.cellphone}`}>{selectedCheckpoint.cellphone}</a></>}
                        {selectedCheckpoint.email && <><a class='ml-3' href={`mailto:${selectedCheckpoint.email}`}><i class='fas fa-envelope' /></a></>}
                        {selectedCheckpoint.facebook && <><a class='ml-3' href={`${mu.asFacebookLink(selectedCheckpoint.facebook)}`}><i class='fab fa-facebook' /></a></>}
                        {selectedCheckpoint.instagram && <><a class='ml-3' href={`${mu.asInstagramLink(selectedCheckpoint.instagram)}`}><i class='fab fa-instagram' /></a></>}
                        {selectedCheckpoint.description && <><a class='ml-3 text-primary' onCLick={this.toggleInfo}><i class='fas fa-info-circle' /></a></>}
                    </div>
                </div>
            </div>
            {showInfo && selectedCheckpoint.description && <div>
                <Markdown markdown={mu.replaceImages(selectedCheckpoint.description, selectedCheckpoint.images)} markdownOpts={mu.getMarkdownOptions()} />
            </div>}
            <div class=''>
                <div class=''>
                    <div class='d-flex justify-content-between mt-2'>
                        <div class='flex-fill bg-light rounded-lg p-3 align-middle'>
                            <div>
                                <small class='text-muted'>
                                    <i class='fas fa-flag' /> Start
                                </small>
                            </div>
                            <div class='text-center'>
                                {util.format(selectedCheckpoint.totalDistance || 0, 0)} <span class='text-muted'>km</span>
                            </div>
                        </div>
                        {!selectedCheckpoint.isStartLine && <div class='flex-fill bg-light rounded-lg p-3 align-middle'>
                            <div>
                                <small class='text-muted'>
                                    <i class='fas fa-arrow-down' /> Prev
                                </small>
                            </div>
                            <div class='text-center'>
                                <nobr>{util.format(selectedCheckpoint.distanceFromPrev || 0, 0)} <span class='text-muted'>km</span></nobr>
                            </div>
                        </div>}
                        {/* <div class='flex-fill bg-light rounded-lg p-3 align-middle'>
                            <div>
                                <small class='text-muted'>
                                    <i class='fas fa-campground' /> CP
                                </small>
                            </div>
                            <div class='text-center'>
                                {selectedCheckpoint.name}
                            </div>
                        </div> */}
                        <div class='flex-fill bg-light rounded-lg p-3 align-middle'>
                            <div>
                                <small class='text-muted'>
                                    <i class='fas fa-arrow-up' /> Next
                                </small>
                            </div>
                            <div class='text-center'>
                                {util.format(selectedCheckpoint.distanceToNext || 0, 0)} <span class='text-muted'>km</span>
                            </div>
                        </div>
                        <div class='flex-fill bg-light rounded-lg p-3 align-middle'>
                            <div>
                                <small class='text-muted'>
                                    <i class='fas fa-flag-checkered' /> Finish
                                </small>
                            </div>
                            <div class='text-center'>
                                <nobr>{util.format(selectedCheckpoint.distanceToFinish || 0, 0)} <span class='text-muted'>km</span></nobr>
                            </div>
                        </div>
                    </div>
                    <div class='d-flex justify-content-between mt-2'>
                        {selectedCheckpoint.requiredRestMinutes && <div class='flex-fill bg-light rounded-lg p-3 align-middle'>
                            <div>
                                <small class='text-muted'>
                                    <i class='fas fa-bed' /> Mandatory rest
                                </small>
                            </div>
                            <div class='text-center'>
                                <nobr>{util.secToHms(selectedCheckpoint.requiredRestMinutes * 60, true)}</nobr>
                                {selectedCheckpoint.timeEqualization && <>
                                    <span class='text-muted ml-2'>+</span> <i class='fas fa-user-clock' /> Time Equalization
                                </>}
                            </div>
                        </div>}
                    </div>
                </div>
            </div>

            <div class='d-flex justify-content-center mb-3'>
                <div class='col px-0 pt-2'>
                    <button
                        type='button'
                        class={`btn btn-block rounded-pill-left rounded-none ${submenu === 'detailsIn' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                        style='text-overflow: ellipsis; overflow: hidden;'
                        onClick={this.chooseSubmenu}
                        data-submenu='detailsIn'
                    >
                        Details in
                    </button>
                </div>
                <div class='col px-0 pt-2'>
                    <button
                        type='button'
                        class={`btn btn-block rounded-none ${submenu === 'detailsOut' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                        style='text-overflow: ellipsis; overflow: hidden;'
                        onClick={this.chooseSubmenu}
                        data-submenu='detailsOut'
                    >
                        Detail out
                    </button>
                </div>
                <div class='col px-0 pt-2'>
                    <button
                        type='button'
                        class={`btn btn-block rounded-pill-right rounded-none ${submenu === 'legTimes' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                        style='text-overflow: ellipsis; overflow: hidden;'
                        onClick={this.chooseSubmenu}
                        data-submenu='legTimes'
                    >
                        Leg times
                    </button>
                </div>
            </div>

            <div
                class='w-100 overflow-auto position-relative'
                style='max-height: 60vh;'
            >
                {/* {
                    "position": [],
                    "id": "2e8f57cb-4bc0-4f5f-b41c-783944be9b8a",
                    "bib": 30,
                    "checkpoint": "e1f8e66f-1629-4ea6-b155-fa8fb3b377ab",
                    "direction": "out",
                    "dogs": 10,
                    "timestamp": "2019-03-12T05:50:00.000Z"
                }, */}

                <div class='table-responsive-lg'>
                    <table class='table table-striped table-sm'>
                        {submenu === 'detailsIn' && <>
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Musher</th>
                                    <th>Time in</th>
                                    <th>Time out</th>
                                    <th>Time used</th>
                                    <th>Avg speed</th>
                                    <th>Dogs in</th>
                                </tr>
                            </thead>
                            <tbody>
                            {lastResultsIn && lastResultsIn.map((res, idx) => {
                                const selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(res.bib, 10)) : null;
                                const resOut = lastResultsOut.find(e => e.bib === parseInt(res.bib, 10));
                                const resIn = lastResultsIn.find(e => e.bib === parseInt(res.bib, 10));
                                const resScratched = lastResultsScratched.find(e => e.bib === parseInt(res.bib, 10));
                                const prevResOut = prevResultsOut && prevResultsOut.find(e => e.bib === parseInt(res.bib, 10));
                                let timeused;
                                let restTime;
                                let avgSpeed = 0;
                                if (prevResOut && res) {
                                    timeused = mu.deltaSec(prevResOut.timestamp, res.timestamp);
                                    avgSpeed = selectedCheckpoint.distanceFromPrev / (timeused / 3600);
                                }
                                if (resIn && resOut && resIn.timestamp && resOut.timestamp) {
                                    restTime = mu.deltaSec(resIn.timestamp, resOut.timestamp);
                                }
                                return (<>
                                    <tr>
                                        <td class='text-center'>{idx + 1}</td>
                                        <td>
                                            <div class='d-flex flex-nowrap'>
                                                <div
                                                    class='text-white font-weight-bolder pl-2 pr-1 py-0 rounded-pill-left rounded-none mr-2'
                                                    style={`background-color: ${mu.getRankingColor(idx + 1)}; width: 45px;`}
                                                >
                                                    {res.bib}
                                                </div>
                                                <div>
                                                    <nobr>
                                                        {selectedMusher.flag} {selectedMusher.lastname}
                                                    </nobr>
                                                </div>
                                            </div>
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {mu.formatDate(resIn.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {resOut && <>
                                                <nobr>
                                                    {mu.formatDate(resOut.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                            {resScratched && <>
                                                <nobr class='text-danger'>
                                                    <i class='fas fa-times' />
                                                    {mu.formatDate(resScratched.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {util.secToHms(timeused, true)}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {util.format(avgSpeed || 0, 1)} <span class='text-muted font-weight-lighter'>km/t</span>
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>{res.dogs}</td>
                                    </tr>
                                    {/* <tr>
                                        <td colspan='8'>
                                            <xmp>{JSON.stringify(prevCp, null, 4)}</xmp>
                                            <xmp>{JSON.stringify(prevResOut, null, 4)}</xmp>
                                            <xmp>{JSON.stringify(selectedMusher, null, 4)}</xmp>
                                        </td>
                                    </tr> */}
                                </>);
                            })}
                            </tbody>
                        </>}
                        {submenu === 'detailsOut' && <>
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Musher</th>
                                    <th>Time in</th>
                                    <th>Time out</th>
                                    <th>Time used</th>
                                    <th>Rest in cp</th>
                                    <th>Avg speed</th>
                                    <th>Dogs out</th>
                                    <th>Next CP</th>
                                </tr>
                            </thead>
                            <tbody>
                            {lastResultsOut && lastResultsOut.map((res, idx) => {
                                let selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(res.bib, 10)) : null;
                                selectedMusher = selectedMusher ? selectedMusher : {};
                                const resIn = lastResultsIn.find(e => e.bib === parseInt(res.bib, 10));
                                const resOut = lastResultsOut.find(e => e.bib === parseInt(res.bib, 10));
                                const resScratched = lastResultsScratched.find(e => e.bib === parseInt(res.bib, 10));
                                const prevResOut = prevResultsOut && prevResultsOut.find(e => e.bib === parseInt(res.bib, 10));
                                const nextResIn = nextResultsIn && nextResultsIn.find(e => e.bib === parseInt(res.bib, 10));
                                let timeused;
                                let restTime;
                                let avgSpeed = 0;
                                if (prevResOut && res) {
                                    timeused = mu.deltaSec(prevResOut.timestamp, res.timestamp);
                                    avgSpeed = selectedCheckpoint.distanceFromPrev / (timeused / 3600);
                                }
                                if (resIn && resOut && resIn.timestamp && resOut.timestamp) {
                                    restTime = mu.deltaSec(resIn.timestamp, resOut.timestamp);
                                }

                                let estTimeInNextCp;
                                let estTimeToNext = selectedCheckpoint.estTimeToCpMinutes || 0;
                                if (estTimeToNext && selectedMusher.cpSortOrder === selectedCheckpoint.sortOrder) {
                                    estTimeInNextCp = mu.addDeltaSec(resOut.timestamp, estTimeToNext * 60);
                                }

                                return (<>
                                    <tr>
                                        <td class='text-center'>{idx + 1}</td>
                                        <td>
                                            <div class='d-flex flex-nowrap'>
                                                <div
                                                    class='text-white font-weight-bolder pl-2 pr-1 py-0 rounded-pill-left rounded-none mr-2'
                                                    style={`background-color: ${mu.getRankingColor(idx + 1)}; width: 45px;`}
                                                >
                                                    {res.bib}
                                                </div>
                                                <div>
                                                    <nobr>
                                                        {selectedMusher.flag} {selectedMusher.lastname}
                                                    </nobr>
                                                </div>
                                            </div>
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {mu.formatDate(resIn.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {resOut && <>
                                                <nobr>
                                                    {mu.formatDate(resOut.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                            {resScratched && <>
                                                <nobr class='text-danger'>
                                                    <i class='fas fa-times' />
                                                    {mu.formatDate(resScratched.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {util.secToHms(timeused, true)}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && restTime && <>
                                                <nobr>
                                                    {util.secToHms(restTime, true)}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {util.format(avgSpeed || 0, 1)} <span class='text-muted font-weight-lighter'>km/t</span>
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {resOut.dogs}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isFinishLine && nextResIn ? <>
                                                <nobr>
                                                    {mu.formatDate(nextResIn.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </> : <>
                                                {estTimeInNextCp && <>
                                                    <nobr class='text-muted'>
                                                        {mu.formatDate(estTimeInNextCp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                    </nobr>
                                                </>}
                                            </>}
                                        </td>                                    
                                    </tr>
                                    {/* <tr>
                                        <td colspan='8'>
                                            <xmp>{JSON.stringify(prevCp, null, 4)}</xmp>
                                            <xmp>{JSON.stringify(prevResOut, null, 4)}</xmp>
                                            <xmp>{JSON.stringify(selectedMusher, null, 4)}</xmp>
                                        </td>
                                    </tr> */}
                                </>);
                            })}
                            </tbody>
                        </>}
                        {submenu === 'legTimes' && <>
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Musher</th>
                                    <th>Time in</th>
                                    <th>Time out</th>
                                    <th>Time used</th>
                                    <th>Avg speed</th>
                                    <th>Dogs in</th>
                                </tr>
                            </thead>
                            <tbody>
                            {lastResultsLegtimes && lastResultsLegtimes.map((res, idx) => {
                                const selectedMusher = sortedContestants ? sortedContestants.find(e => e.bib === parseInt(res.bib, 10)) : null;
                                const resIn = lastResultsIn.find(e => e.bib === parseInt(res.bib, 10));
                                const resOut = lastResultsOut.find(e => e.bib === parseInt(res.bib, 10));
                                const resScratched = lastResultsScratched.find(e => e.bib === parseInt(res.bib, 10));
                                const prevResOut = prevResultsOut && prevResultsOut.find(e => e.bib === parseInt(res.bib, 10));
                                let timeused;
                                let avgSpeed = 0;
                                if (prevResOut && res) {
                                    timeused = mu.deltaSec(prevResOut.timestamp, res.timestamp);
                                    avgSpeed = selectedCheckpoint.distanceFromPrev / (timeused / 3600);
                                }
                                return (<>
                                    <tr>
                                        <td class='text-center'>{idx + 1}</td>
                                        <td>
                                            <div class='d-flex flex-nowrap'>
                                                <div
                                                    class='text-white font-weight-bolder pl-2 pr-1 py-0 rounded-pill-left rounded-none mr-2'
                                                    style={`background-color: ${mu.getRankingColor(idx + 1)}; width: 45px;`}
                                                >
                                                    {res.bib}
                                                </div>
                                                <div>
                                                    <nobr>
                                                        {selectedMusher.flag} {selectedMusher.lastname}
                                                    </nobr>
                                                </div>
                                            </div>
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {mu.formatDate(resIn.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {resOut && <>
                                                <nobr>
                                                    {mu.formatDate(resOut.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                            {resScratched && <>
                                                <nobr class='text-danger'>
                                                    <i class='fas fa-times' />
                                                    {mu.formatDate(resScratched.timestamp, { locale: language, day: 'numeric', month: 'numeric', hour12: false, hour: '2-digit', minute: '2-digit' })}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {util.secToHms(timeused, true)}
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>
                                            {!selectedCheckpoint.isStartLine && <>
                                                <nobr>
                                                    {util.format(avgSpeed || 0, 1)} <span class='text-muted font-weight-lighter'>km/t</span>
                                                </nobr>
                                            </>}
                                        </td>
                                        <td class='text-center'>{res.dogs}</td>
                                    </tr>
                                    {/* <tr>
                                        <td colspan='8'>
                                            <xmp>{JSON.stringify(prevCp, null, 4)}</xmp>
                                            <xmp>{JSON.stringify(prevResOut, null, 4)}</xmp>
                                            <xmp>{JSON.stringify(selectedMusher, null, 4)}</xmp>
                                        </td>
                                    </tr> */}
                                </>);
                            })}
                            </tbody>
                        </>}
                    </table>
                </div>
            </div>

            {/* <h3>RaceLiveCheckpointResult</h3> */}
            {/* <h5>{id} {selectedCheckpoint.name}</h5> */}
            {/* <xmp>{JSON.stringify(selectedCheckpoint, null, 4)}</xmp> */}
            {/* <xmp>{JSON.stringify(selectedCheckpoint, null, 4)}</xmp> */}
            {/* <xmp>{JSON.stringify(lastResultsIn, null, 4)}</xmp> */}
        </>);
    }
}

export default RaceLiveCheckpointResult;
