import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import mu from '../../lib/musher-util';

const initialState = {
	isLoading: false,
};

const fieldSorter = (fields) => (a, b) => fields.map(o => {
    let dir = 1;
    if (o[0] === '-') { dir = -1; o=o.substring(1); }
    return a[o] > b[o] ? dir : a[o] < b[o] ? -(dir) : 0;
}).reduce((p, n) => p ? p : n, 0);

@observer
class RaceLiveResults extends Component {
    componentWillReceiveProps(nextProps) {
        if (this.props.currentClass.id !== nextProps.currentClass.id || this.props.reloadLastEpoch !== nextProps.reloadLastEpoch) {
            const { currentClass } = this.props;
            const { raceStore } = this.props.stores;
            const { checkpoints = [], contestants = [], results = [] } = currentClass;
            raceStore.sortContestantsByResult({ contestants, results, checkpoints });
        }
	}

    componentDidMount() {
        const { currentClass } = this.props;
        const { raceStore } = this.props.stores;
        const { checkpoints = [], contestants = [], results = [] } = currentClass;
        raceStore.sortContestantsByResult({ contestants, results, checkpoints });
	}

    render() {
        const widthCp = 20;
        const width = 40;
		const {
            race,
            currentClass,
            onClickMusher = () => {},
            onClickCheckpoint = () => {},
            selectedCheckpointId,
            limit = 100,
            tableClass = 'table-striped table-sm',
            thClass,
            thStyle,
            tdClass,
            tdStyle,
            progressStyle = 'height: 20px;',
            progressbarStyle,
        } = this.props;
        const { checkpoints = [], results = [] } = currentClass;
        const { raceStore } = this.props.stores;
        const { sortedContestants = [] } = raceStore;
        const dotColor = '#808080';

		return (<>
            <div class='table-responsive'>
                <table class={`table ${tableClass}`}>
                    <thead>
                        <tr>
                            <th class={thClass} style={thStyle}>
                                <div class='' style={`width: ${width}px;`}>
                                    <i class='fas fa-flag' />
                                </div>
                            </th>
                            {checkpoints && checkpoints.slice().sort(fieldSorter(['sortOrder'])).map((cp, cpIdx) => <>
                                {cpIdx > 0 && <th class={thClass} style={thStyle}>
                                    <div class='' style={`width: ${width}px;`}>
                                        &nbsp;
                                    </div>
                                </th>}
                                <th class={thClass} style={thStyle}>
                                    <div
                                        class={`checkpoint position-relative ${selectedCheckpointId === cp.id ? 'text-primary' : ''}`}
                                        style={`width: ${widthCp}px; font-size: 1.1em;`}
                                        onClick={onClickCheckpoint}
                                        data-id={cp.id}
                                    >
                                        <i class='fas fa-campground' />

                                        <div
                                            class={`rounded-pill-both px-1 text-center position-absolute ${dotColor ? '' : 'bg-secondary'} font-weight-bold`}
                                            style={`bottom: 0px; right: -20px; font-size: 0.6em; ${dotColor ? `background-color: ${dotColor};` : ''}`}
                                        >
                                            {cp.sortOrder && cp.sortOrder !== 'n/a' && <>{cp.sortOrder}</>}
                                        </div>

                                    </div>
                                </th>
                            </>)}
                            <th class={thClass} style={thStyle}>
                                <div class='' style={`width: ${widthCp}px;`}>
                                    <i class='fas fa-flag-checkered' />
                                </div>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {sortedContestants && sortedContestants.slice(0, limit).map((co, coIdx) => {
                            let cpIdx = 0;
                            let hasLeftCpPrev;
                            let bibIdxOutPrev;
                            let estTimeToCpMinutesPrev;
                            return(<>
                                <tr class='musher' onClick={onClickMusher} data-bib={co.bib}>
                                    <td class={tdClass} style={tdStyle}>
                                        {co.startTime ? util.isoTime(co.startTime) : ''}
                                    </td>
                                    {checkpoints && checkpoints.slice().sort(fieldSorter(['sortOrder'])).map((cp, idx) => {
                                        const res = results.slice().sort(fieldSorter(['timestamp'])).filter(e => parseInt(e.bib, 10) == co.bib && e.checkpoint === cp.id);
                                        const hasLeftCp = res.find(e => e.direction === 'out');
                                        const hasArrivedCp = res.find(e => e.direction === 'in');
                                        const hasScratched= res.find(e => e.direction === 'scratched');

                                        const allResultsIn = results.slice().sort(fieldSorter(['timestamp'])).filter(e => e.direction === 'in' && e.checkpoint === cp.id);
                                        const bibIdxIn = allResultsIn.findIndex(e => parseInt(e.bib, 10) == co.bib);

                                        const allResultsOut = results.slice().sort(fieldSorter(['timestamp'])).filter(e => e.direction === 'out' && e.checkpoint === cp.id);
                                        const bibIdxOut = allResultsOut.findIndex(e => parseInt(e.bib, 10) == co.bib);

                                        if (bibIdxIn === -1)  {
                                            if (hasLeftCpPrev) {
                                                const timeUsedOnStage = Math.floor(new Date(hasLeftCpPrev.timestamp).getTime() / 1000 / 60);
                                                let percentDone = timeUsedOnStage / estTimeToCpMinutesPrev * 100;
                                                percentDone = percentDone > 95 ? 95 : percentDone;
                                                const line = (<>
                                                    <td class={`text-center align-middle px-0 pr-1 ${tdClass}`} style={tdStyle}>
                                                        <div class='progress' style={`${progressStyle}`}>
                                                            <div
                                                                class={`progress-bar progress-bar-striped progress-bar-animated`}
                                                                role='progressbar'
                                                                style={`width: ${percentDone}%; ${progressbarStyle}`}
                                                                aria-valuenow={percentDone}
                                                                aria-valuemin='0'
                                                                aria-valuemax='100'
                                                            >{bibIdxOutPrev + 1 > 0 ? bibIdxOutPrev + 1 : ''}</div>
                                                        </div>

                                                        {/* estTimeToCpMinutesPrev: {estTimeToCpMinutesPrev}
                                                        timeUsedOnStage: {timeUsedOnStage}
                                                        percentDone: {percentDone} */}
                                                        {co.cpId && res.length > 0 && <>
                                                            {/* <xmp>{JSON.stringify(co, null, 4)}</xmp><br /> */}
                                                            {/* <xmp>{JSON.stringify(res, null, 4)}</xmp><br /> */}
                                                            {/* <xmp>{JSON.stringify(allResults, null, 4)}</xmp><br /> */}
                                                        </>}
                                                    </td>
                                                </>);
                                                bibIdxOutPrev = bibIdxOut;
                                                hasLeftCpPrev = hasLeftCp;
                                                estTimeToCpMinutesPrev = cp.estTimeToCpMinutes;
                                                return line;
                                            }
                                            return '';
                                        }
                                        cpIdx = idx;
                                        bibIdxOutPrev = bibIdxOut;
                                        hasLeftCpPrev = hasLeftCp;
                                        estTimeToCpMinutesPrev = cp.estTimeToCpMinutes;
                                        return (<>
                                            {hasArrivedCp && idx > 0 && <>
                                                <td class={`text-center align-middle px-0 pr-1 ${tdClass}`} style={tdStyle}>
                                                    <div class='progress' style={`${progressStyle}`}>
                                                        <div
                                                            class={`progress-bar`}
                                                            role='progressbar'
                                                            style={`width: 100%; ${progressbarStyle}`}
                                                            aria-valuenow='10'
                                                            aria-valuemin='0'
                                                            aria-valuemax='100'
                                                        />
                                                    </div>
                                                    {co.cpId && res.length > 0 && <>
                                                        {/* <xmp>{JSON.stringify(co, null, 4)}</xmp><br /> */}
                                                        {/* <xmp>{JSON.stringify(res, null, 4)}</xmp><br /> */}
                                                        {/* <xmp>{JSON.stringify(allResults, null, 4)}</xmp><br /> */}
                                                    </>}
                                                </td>
                                            </>}
                                            <td class={`text-center align-middle px-0 pr-1 ${tdClass}`} style={tdStyle}>
                                                <div class='progress' style={`${progressStyle}`}>
                                                    <div
                                                        class={`progress-bar ${hasScratched ? 'bg-danger' : 'bg-success'}`}
                                                        role='progressbar'
                                                        style={`width: 100%; ${progressbarStyle}`}
                                                        aria-valuenow='10'
                                                        aria-valuemin='0'
                                                        aria-valuemax='100'
                                                    >{bibIdxIn + 1 > 0 ? bibIdxIn + 1 : ''}</div>
                                                </div>
                                                {co.cpId && res.length > 0 && <>
                                                    {/* <xmp>{JSON.stringify(co, null, 4)}</xmp><br /> */}
                                                    {/* <xmp>{JSON.stringify(res, null, 4)}</xmp><br /> */}
                                                    {/* <xmp>{JSON.stringify(allResults, null, 4)}</xmp><br /> */}
                                                </>}
                                            </td>
                                        </>);
                                    })}
                                    <td colspan={checkpoints.length * 2 - cpIdx} class={tdClass} style={tdStyle}>
                                        <div class='d-flex flex-nowrap'>
                                            <div
                                                class='text-white font-weight-bolder pl-2 pr-1 py-0 rounded-pill-left rounded-none mr-2'
                                                style={`background-color: ${mu.getRankingColor(coIdx + 1)}; width: 45px;`}
                                            >
                                                {co.bib}
                                            </div>
                                            <div>
                                                <nobr>
                                                    {co.flag} {co.lastname}
                                                </nobr>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            </>);
                        })}
                    </tbody>
                </table>
            </div>
		</>);
	}
}

export default RaceLiveResults;
